
:root {
  --light-grey: #7B8DAD;
  --bcolor: #182435;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: var(--light-grey);
  margin-top: 70px;
}
body {
  background-color: var(--bcolor);
}
.audiolist {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 5px;
  padding:5px;
  margin: 0 auto;
}
.hidden {
  display: none;
}
.visible {
  display: none;
}
.backbtn {
  position: fixed;
 margin: 0 auto;
 left: 2em;
 top: 2em;
}
.backbtn :hover {
  background-color: var(--light-grey);
  color: var(--bcolor);
}
button {
  color: #C6CDE0;
  font-size: 1em;
  border: solid 1px var(--light-grey);
  border-radius: 3px;
  background-color: var(--bcolor);
  padding: 10px 20px;
  margin-bottom: 2em;
}




